import { Helmet } from "react-helmet";

export function OG(props: { title?: string; description?: string; url?: string; image?: string }) {
  const {
    title = "HoseungJang WIKI",
    description = "장호승 개인 위키",
    url = window.location.href,
    image = `${window.location.origin}${process.env.PUBLIC_URL}/og_image.jpg`,
  } = props;

  return (
    <Helmet>
      <title>{title}</title>
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:url" content={url} />
      <meta property="og:image" content={image} />
    </Helmet>
  );
}
